* {
  box-sizing: border-box;
}

html {
  --colour-background: #102542;
  --colour-text: #FFFFFF;
  --colour-three: #CDD7D6;
  --colour-four: #B3A394;
  --colour-highlight: #F87060;
}

html {
  font-size: 1.5em;
  background-color: var(--colour-background);
}

.app {
  text-align: center;
  height: 100dvh;
  color: var(--colour-text);
  font-family: Helvetica Neue, Helvetica, sans-serif;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  padding: 2rem 2rem 1rem;
  max-width: 920px;
  margin: 0 auto;
}

.question {
  margin: 0 0 2rem;
  line-height: 1.25rem;
  min-height: 2.5rem;
  color: var(--colour-text);
}

p.small {
  font-size: 0.8rem;
}

a {
  color: var(--colour-highlight);
  text-decoration: underline;
  cursor: pointer;
}

.step {
  display: block;
  flex: 1;
}

.step.hidden {
  display: none;
}

input {
  margin: 0 0 2rem;
  background: var(--colour-three);
  padding: 0.5rem 1rem;
  font-size: 1rem;
  width: 100%;
  border: 2px solid transparent;
  border-radius: 0.1rem;
  outline: none;
}

input:active, input:focus {
  border-color: var(--colour-text);
  outline: none;
}

.spacer {
  height: 1rem;
}


.quickbuttons {
  display: flex;
  justify-content: center;
}

button {
  margin: 0 0.25rem;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  background: var(--colour-three);
  color: var(--colour-background);
  border: 2px solid transparent;
  border-radius: 0.1rem;
}

button.linkbutton {
  background: none;
  color: var(--colour-highlight);
  text-decoration: underline;
  font-size: inherit;
}

button.active {
  background: var(--colour-four);
  border-color: white;
}

.quickbuttons button {
  font-size: 0.8rem;
  padding: 0.25rem 0.5rem;
}

.quickbuttons.large button {
  font-size: 1rem;
  padding: 0.5rem 1rem;
}

.navbutton {
  margin: 0.5rem;
  background: var(--colour-three);
  color: var(--colour-background);
  border: none;
  border-radius: 0.1rem;
}


.summary ul {
  margin: 2rem 0;
  text-align: left;
  padding: 0;
}

.summary ul li {
  list-style-type: none;
}

.summary ul li.total {
  margin-top: 1rem;
}

.summary .highlight {
  color: var(--colour-highlight);
  font-weight: bold;
}

.explainer {
  text-align: left;
}